.vczapi-blocks-form {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 100%; }
  .vczapi-blocks-form .radio-inline .components-base-control__field {
    display: flex;
    align-content: center; }
    .vczapi-blocks-form .radio-inline .components-base-control__field label {
      margin-right: 10px; }
  .vczapi-blocks-form--input-label {
    width: 100%; }
  .vczapi-blocks-form--group {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 1.5em; }
  .vczapi-blocks-form--select {
    flex: 1 1 auto;
    margin: 0 8px 0 0; }
  .vczapi-blocks-form--selected-meeting {
    width: 100%;
    margin-bottom: 2em;
    border-bottom: 1px solid; }
  .vczapi-blocks-form .vczapi-blocks-pagination {
    width: 100%;
    display: flex;
    /* align-items: center; */
    justify-content: center;
    margin-top: 20px; }
    .vczapi-blocks-form .vczapi-blocks-pagination button.selected {
      background: var(--wp-admin-theme-color);
      color: #fff; }
